import axios from 'axios';

const API_BASE_URL = process.env.REACT_APP_API_BASE_URL || 'http://localhost:8000/api';

const apiService = axios.create({
  baseURL: API_BASE_URL,
});

apiService.interceptors.request.use((config) => {
  const token = localStorage.getItem('token');
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

const handleResponse = (response) => response.data;
const handleError = (error) => {
  console.error('API call error:', error);
  throw error;
};

const apiEndpoints = {
  assignments: {
    getAll: (page = 1) => apiService.get(`/assignments/?page=${page}`).then(handleResponse).catch(handleError),
    get: (id) => apiService.get(`/assignments/${id}/`).then(handleResponse).catch(handleError),
    create: (data) => apiService.post('/assignments/', data).then(handleResponse).catch(handleError),
    update: (id, data) => apiService.put(`/assignments/${id}/`, data).then(handleResponse).catch(handleError),
    delete: (id) => apiService.delete(`/assignments/${id}/`).then(handleResponse).catch(handleError),
    getStatus: (id) => apiService.get(`/assignments/${id}/status/`).then(handleResponse).catch(handleError),
    approveRubric: (id) => apiService.post(`/assignments/${id}/approve_rubric/`).then(handleResponse).catch(handleError),
    generateRubric: (id) => apiService.post(`/assignments/${id}/generate_rubric/`).then(handleResponse).catch(handleError),
    getScormSummary: (id) => apiService.get(`/assignments/${id}/scorm_summary/`).then(handleResponse).catch(handleError),
    getScormDetailedReport: (id) => apiService.get(`/assignments/${id}/scorm_detailed_report/`).then(handleResponse).catch(handleError),
  },

  submissions: {
    getAll: (assignmentId) => apiService.get(`/submissions/?assignment=${assignmentId}`).then(handleResponse).catch(handleError),
    get: (id) => apiService.get(`/submissions/${id}/`).then(handleResponse).catch(handleError),
    create: (data) => apiService.post('/submissions/', data).then(handleResponse).catch(handleError),
    update: (id, data) => apiService.put(`/submissions/${id}/`, data).then(handleResponse).catch(handleError),
    delete: (id) => apiService.delete(`/submissions/${id}/`).then(handleResponse).catch(handleError),
    submitText: (data) => apiService.post('/submissions/submit_text/', data).then(handleResponse).catch(handleError),
    uploadFile: (data) => apiService.post('/submissions/upload_file/', data, {
      headers: { 'Content-Type': 'multipart/form-data' }
    }).then(handleResponse).catch(handleError),
    bulkSubmit: (data) => apiService.post('/submissions/bulk_submit/', data).then(handleResponse).catch(handleError),
    withKnowledgeBaseItems: (assignmentId) => apiService.get(`/submissions/with_knowledge_base_items/?assignment=${assignmentId}`).then(handleResponse).catch(handleError),
    recordInteraction: (id, data) => apiService.post(`/submissions/${id}/record_interaction/`, data).then(handleResponse).catch(handleError),
    startSession: (id) => apiService.post(`/submissions/${id}/start_session/`).then(handleResponse).catch(handleError),
    endSession: (id) => apiService.post(`/submissions/${id}/end_session/`).then(handleResponse).catch(handleError),
    getScormData: (id) => apiService.get(`/submissions/${id}/scorm_data/`).then(handleResponse).catch(handleError),
    updateScormData: (id, data) => apiService.post(`/submissions/${id}/update_scorm_data/`, data).then(handleResponse).catch(handleError),
  },

  rubrics: {
    getAll: (assignmentId) => apiService.get(`/rubrics/?assignment=${assignmentId}`).then(handleResponse).catch(handleError),
    get: (id) => apiService.get(`/rubrics/${id}/`).then(handleResponse).catch(handleError),
    create: (data) => apiService.post('/rubrics/', data).then(handleResponse).catch(handleError),
    update: (id, data) => apiService.put(`/rubrics/${id}/`, data).then(handleResponse).catch(handleError),
    delete: (id) => apiService.delete(`/rubrics/${id}/`).then(handleResponse).catch(handleError),
  },

  grades: {
    getAll: () => apiService.get('/grades/').then(handleResponse).catch(handleError),
    get: (id) => apiService.get(`/grades/${id}/`).then(handleResponse).catch(handleError),
    create: (data) => apiService.post('/grades/', data).then(handleResponse).catch(handleError),
    update: (id, data) => apiService.put(`/grades/${id}/`, data).then(handleResponse).catch(handleError),
    delete: (id) => apiService.delete(`/grades/${id}/`).then(handleError),
    override: (id, data) => apiService.post(`/grades/${id}/override/`, data).then(handleResponse).catch(handleError),
    getScormData: (id) => apiService.get(`/grades/${id}/scorm_data/`).then(handleResponse).catch(handleError),
  },

  critiques: {
    getAll: () => apiService.get('/critiques/').then(handleResponse).catch(handleError),
    get: (id) => apiService.get(`/critiques/${id}/`).then(handleResponse).catch(handleError),
    create: (data) => apiService.post('/critiques/', data).then(handleResponse).catch(handleError),
    update: (id, data) => apiService.put(`/critiques/${id}/`, data).then(handleResponse).catch(handleError),
    delete: (id) => apiService.delete(`/critiques/${id}/`).then(handleError),
    approve: (id) => apiService.post(`/critiques/${id}/approve/`).then(handleResponse).catch(handleError),
    getScormData: (id) => apiService.get(`/critiques/${id}/scorm_data/`).then(handleResponse).catch(handleError),
  },

  scormAPI: {
    initialize: (assignmentId, submissionId) => apiService.post('/scorm-api/LMSInitialize/', { assignment_id: assignmentId, submission_id: submissionId }).then(handleResponse).catch(handleError),
    finish: (assignmentId, submissionId) => apiService.post('/scorm-api/LMSFinish/', { assignment_id: assignmentId, submission_id: submissionId }).then(handleResponse).catch(handleError),
    getValue: (assignmentId, submissionId, element) => apiService.post('/scorm-api/LMSGetValue/', { assignment_id: assignmentId, submission_id: submissionId, element }).then(handleResponse).catch(handleError),
    setValue: (assignmentId, submissionId, element, value) => apiService.post('/scorm-api/LMSSetValue/', { assignment_id: assignmentId, submission_id: submissionId, element, value }).then(handleResponse).catch(handleError),
    commit: (assignmentId, submissionId) => apiService.post('/scorm-api/LMSCommit/', { assignment_id: assignmentId, submission_id: submissionId }).then(handleResponse).catch(handleError),
    getLastError: () => apiService.post('/scorm-api/LMSGetLastError/').then(handleResponse).catch(handleError),
    getErrorString: (errorCode) => apiService.post('/scorm-api/LMSGetErrorString/', { error_code: errorCode }).then(handleResponse).catch(handleError),
    getDiagnostic: (errorCode) => apiService.post('/scorm-api/LMSGetDiagnostic/', { error_code: errorCode }).then(handleResponse).catch(handleError),
    getVersion: () => apiService.post('/scorm-api/LMSGetVersion/').then(handleResponse).catch(handleError),
    setStatus: (assignmentId, submissionId, status) => apiService.post('/scorm-api/LMSSetStatus/', { assignment_id: assignmentId, submission_id: submissionId, status }).then(handleResponse).catch(handleError),
    getEntry: (assignmentId, submissionId) => apiService.post('/scorm-api/LMSGetEntry/', { assignment_id: assignmentId, submission_id: submissionId }).then(handleResponse).catch(handleError),
  },
};

export default apiEndpoints;