import axios from 'axios';

export const API_BASE_URL = process.env.REACT_APP_API_BASE_URL || 'http://localhost:8000/api';

const api = axios.create({
  baseURL: API_BASE_URL,
});

api.interceptors.request.use((config) => {
  const token = localStorage.getItem('token');
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

api.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response && error.response.status === 401) {
      localStorage.removeItem('token');
      window.location.href = '/login';
    }
    return Promise.reject(error);
  }
);

// Add SCORM-specific API call
api.scorm = {
  setValue: async (assignmentId, submissionId, element, value) => {
    try {
      const response = await api.post('/scorm-api/LMSSetValue/', {
        assignment_id: assignmentId,
        submission_id: submissionId,
        element,
        value
      });
      return response.data;
    } catch (error) {
      console.error('Error setting SCORM value:', error);
      throw error;
    }
  },
  getValue: async (assignmentId, submissionId, element) => {
    try {
      const response = await api.post('/scorm-api/LMSGetValue/', {
        assignment_id: assignmentId,
        submission_id: submissionId,
        element
      });
      return response.data;
    } catch (error) {
      console.error('Error getting SCORM value:', error);
      throw error;
    }
  },
  commit: async (assignmentId, submissionId) => {
    try {
      const response = await api.post('/scorm-api/LMSCommit/', {
        assignment_id: assignmentId,
        submission_id: submissionId
      });
      return response.data;
    } catch (error) {
      console.error('Error committing SCORM data:', error);
      throw error;
    }
  }
};

export default api;