import axios from 'axios';
import api from './api';

const API_BASE_URL = process.env.REACT_APP_API_BASE_URL || 'http://localhost:8000/api';

export const updateSubmission = async (submissionId, updatedData) => {
  try {
    const response = await axios.patch(`${API_BASE_URL}/submissions/${submissionId}/`, updatedData);

    // Update SCORM data if it's included in the updatedData
    if (updatedData.scorm_data) {
      await api.scorm.setValue(updatedData.assignment_id, submissionId, 'cmi.core.lesson_status', updatedData.scorm_data.cmi_core_lesson_status);
      await api.scorm.setValue(updatedData.assignment_id, submissionId, 'cmi.core.score.raw', updatedData.scorm_data.cmi_core_score_raw);
      await api.scorm.commit(updatedData.assignment_id, submissionId);
    }

    return response.data;
  } catch (error) {
    console.error('Error updating submission:', error);
    throw error;
  }
};

export const fetchKnowledgeBaseItem = async (itemType, itemId) => {
  try {
    const response = await axios.get(`${API_BASE_URL}/${itemType}s/${itemId}/`);

    // Fetch SCORM data if the item is a submission
    if (itemType === 'submission') {
      const scormData = await api.scorm.getValue(response.data.assignment, itemId, 'cmi.core.lesson_status');
      response.data.scorm_data = { cmi_core_lesson_status: scormData.result };
    }

    return response.data;
  } catch (error) {
    console.error(`Error fetching ${itemType}:`, error);
    throw error;
  }
};

export const fetchSubmissionsWithCycles = async (assignmentId) => {
  try {
    const response = await axios.get(`${API_BASE_URL}/submissions/with_knowledge_base_items?assignment=${assignmentId}`);

    // Fetch SCORM data for each submission
    for (let submission of response.data) {
      const scormData = await api.scorm.getValue(assignmentId, submission.id, 'cmi.core.lesson_status');
      submission.scorm_data = { cmi_core_lesson_status: scormData.result };
    }

    return response.data;
  } catch (error) {
    console.error('Error fetching submissions with cycles:', error);
    throw error;
  }
};

export const updateKnowledgeBaseItem = async (itemType, itemId, updatedData) => {
  try {
    if (itemType === 'initial_grade' || itemType === 'final_grade') {
      itemType = 'grade';
    }
    console.log(itemType);
    const response = await axios.patch(`${API_BASE_URL}/${itemType}/${itemId}/`, updatedData);

    // Update SCORM data if the item is a grade
    if (itemType === 'grade') {
      await api.scorm.setValue(updatedData.assignment_id, updatedData.submission_id, 'cmi.core.score.raw', updatedData.content.overall_score);
      await api.scorm.commit(updatedData.assignment_id, updatedData.submission_id);
    }

    return response.data;
  } catch (error) {
    console.error(`Error updating ${itemType}:`, error);
    throw error;
  }
};

export const approveKnowledgeBaseItem = async (itemType, updatedContent) => {
  if (itemType === 'initial_grade' || itemType === 'final_grade') {
    itemType = 'grade';
  }
  console.log(itemType);

  if (!updatedContent || !updatedContent.id) {
    throw new Error(`Invalid item: ${JSON.stringify(updatedContent)}`);
  }
  try {
    updatedContent.human_approved = true
    let updatedItem = updatedContent;  // Include the updated content

    if (itemType === 'critique' && updatedItem.grade) {
      updatedItem.grade = updatedItem.grade.id;
    }

    const response = await axios.patch(`${API_BASE_URL}/${itemType}s/${updatedItem.id}/`, updatedItem);

    // Update SCORM data if the item is a grade
    if (itemType === 'grade') {
      await api.scorm.setValue(updatedItem.assignment_id, updatedItem.submission_id, 'cmi.core.lesson_status', 'completed');
      await api.scorm.commit(updatedItem.assignment_id, updatedItem.submission_id);
    }

    return response.data;
  } catch (error) {
    console.error(`Error approving ${itemType}:`, error);
    throw error;
  }
};

export const createKnowledgeBaseItem = async (itemType, data) => {
  try {
    const response = await axios.post(`${API_BASE_URL}/${itemType}s/`, data);

    // Update SCORM data if the item is a grade
    if (itemType === 'grade') {
      await api.scorm.setValue(data.assignment_id, data.submission_id, 'cmi.core.score.raw', data.content.overall_score);
      await api.scorm.setValue(data.assignment_id, data.submission_id, 'cmi.core.lesson_status', 'completed');
      await api.scorm.commit(data.assignment_id, data.submission_id);
    }

    return response.data;
  } catch (error) {
    console.error(`Error creating ${itemType}:`, error);
    throw error;
  }
};

export const deleteKnowledgeBaseItem = async (itemType, itemId) => {
  try {
    await axios.delete(`${API_BASE_URL}/${itemType}s/${itemId}/`);
    // Note: We don't update SCORM data on deletion as it might not be appropriate to remove SCORM data when an item is deleted
  } catch (error) {
    console.error(`Error deleting ${itemType}:`, error);
    throw error;
  }
};